.products-card, .products-wallet {
    flex: 1 1;
    min-height: 470px;
}

.product-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 40px 24px 28px;
}
.products-card {
    margin-left: 32px;
}
.products-iban {
    margin-top: 32px;
    min-height: 306px;
}
.tile {
    background: #fff;
    border-radius: 16px;
    box-shadow: 0 0 40px rgba(0,0,0,.1);
    height: 100%;
    padding: 24px;
    position: relative;
    width: 100%;
}

.product-card-text-title {
    font-family: Spartan,sans-serif;
    font-size: 48px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: .03em;
    line-height: 54px;
}

.product-card-text-info {
    font-family: Roboto,sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: .03em;
    line-height: 23px;
    margin-top: 36px;
    position: relative;
    z-index: 2;
}
.product-card-footer {
    display: flex;
}

.green-redirect {
    align-items: center;
    color: #367a54;
    display: flex;
    z-index: 1;
}
.green-redirect:hover {
    cursor: pointer;
    transition: .3s;
}
.products-wallet-image {
    bottom: -15px;
}

.product-card-footer-mobile {
    display: none;
}
.product-card-footer-image, .product-card-footer-mobile {
    bottom: 0;
    position: absolute;
    right: 0;
    border-radius: 16px;
}