.support {
    padding-top: 40px;
}

.support-container {
    display: flex;
    flex-direction: column;
}

.support-title {
    display: flex;
    font-family: Spartan,sans-serif;
    font-size: 26px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: 0.06em;
    line-height: 29px;
}

.support-content {
    display: flex;
    flex-direction: column;
    margin-top: 32px;
    width: 100%;
}

.support-menu-link {
    font-family: Spartan,sans-serif;
    font-size: 21px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: 0.06em;
    line-height: 24px;
    margin-top: 32px;
}

.support-container-contact {
    margin-top: 120px;
}

.contact-us {
    font-family: Spartan,sans-serif;
    font-size: 21px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: 0.06em;
    line-height: 24px;
}
.contact-us-clickable {
    color: #367a54;
}

.App-link-support{
    color: #000;
    align-items: center;
    display: flex;
    text-decoration: none;
    text-decoration: initial;
}