.support {
    padding-top: 40px;
}
.page-main {
    background: #eae9e5;
    border-top: 1px solid #c4c4c4;
    height: auto;
    padding-bottom: 100px;
}
.support-container {
    display: flex;
    flex-direction: column;
}

.support-title {
    display: flex;
    font-family: Spartan,sans-serif;
    font-size: 26px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: .03em;
    line-height: 29px;
}
.support-title-back {
    margin-right: 16px;
}
.navigate-back-svg {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}
.support-content {
    display: flex;
    flex-direction: column;
    margin-top: 32px;
    width: 100%;
}
.tile {
    background: #fff;
    border-radius: 16px;
    box-shadow: 0 0 40px rgba(0,0,0,.1);
    height: 100%;
    padding: 24px;
    position: relative;
    width: 100%;
}
.support-account {
    width: auto;
}
.support-country, .support-country-tile {
    width: auto;
}

.support-html-block-tile {
    width: auto;
}
.support-html-block-item-title {
    font-family: Spartan,sans-serif;
    font-size: 21px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: .03em;
    line-height: 24px;
    padding-bottom: 24px;
}

.support-subtitle {
    font-family: Spartan,sans-serif;
    font-size: 21px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: .03em;
    line-height: 24px;
    padding-bottom: 24px;
}
.support-line {
    border-bottom: 1px solid #c4c4c4;
    margin: 32px 0;
}
.container {
    display: flex;
    margin: 0 auto;
    max-width: 1360px;
}


.support-country-high {
    margin-top: 30px;
}
.support-country-medium {
    border-top: none;
}
.sc-table {
    border: 1px solid #c1c7d0;
    display: flex;
}
.sc-table-label {
    width: 200px;
}
.sc-table-content, .sc-table-label {
    padding: 10px;
}
.sc-table-content {
    border-left: 1px solid #c1c7d0;
    display: flex;
    flex-direction: column;
    width: 100%;
}
.sc-row {
    display: flex;
}
.sc-table-content-reason {
    font-family: PT Sans,sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: -.016px;
    line-height: 20px;
    margin-top: 10px;
}
.sc-table-header {
    font-weight: 700;
}
.sc-row-item {
    border-left: 1px solid #c1c7d0;
    border-top: 1px solid #c1c7d0;
    padding: 10px;
    text-overflow: ellipsis;
    white-space: pre-wrap;
    width: 20%;
}
.sc-row-item:last-child {
    border-right: 1px solid #c1c7d0;
}
.sc-row-item:last-child {
    border-right: 1px solid #c1c7d0;
}
.sc-row-item-last {
    border-bottom: 1px solid #c1c7d0;
}

@media (max-width: 1400px){
.container {
    margin: 0 20px;
    width: auto;
}}