.footer {
    background-color: #6a6a6a;
    color: #fff;
    margin-top: auto;
    padding: 30px 0 30px 30px;
    position: relative;
}

.footer-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.footer-nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.footer-nav-logo {
    display: flex;
}
.logo {
    display: flex;
}
.footer-nav-logo-divider {
    margin: 0 12px;
}
.footer-nav-links {
    display: flex;
    justify-content: space-between;
    text-align: center;
}

.route-link {
    align-items: center;
    color: #6a6a6a;
    display: flex;
    text-decoration: none;
    text-decoration: initial;
}
.footer-nav-links-text {
    cursor: pointer;
    color: #fff!important;
    font-family: Roboto,sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: .03em;
    line-height: 19px;
    text-transform: uppercase;
}
.footer-rights {
    display: flex;
    justify-content: space-between;
    margin-top: 52px;
}
.footer-nav-links-text-arrow {
    display: none;
}
.footer-nav-links, .footer-nav-logo, .footer-nav-social {
    flex: 1 1;
}
.footer-content-reserved {
    display: none;
}


@media (max-width: 768px){
    
    .footer {
        padding: 49px 0 45px;
    }

    .footer-content {
        margin: 0;
    }
    .footer-nav {
        flex-direction: column;
    }

    .footer-rights {
        flex-direction: column;
        margin-top: 40px;
        padding: 0 16px;
    }
    .footer-content-reserved {
        display: flex;
        justify-content: center;
        margin-top: 60px;
    }
    .footer-rights-country:last-child {
        color: #eae9e5;
        margin: 16px 0 0;
    }
    .country-info-gray {
        color: #eae9e5;
    }
    .country-info-reg {
        margin-top: 16px;
    }
   
    .footer-content-reserved {
        display: flex;
        justify-content: center;
        margin-top: 60px;
    }
    .footer-nav-logo {
        padding: 0 16px 26px;
    }
    .footer-nav-social {
        display: none;
    }
    .footer-nav-links-text {
        display: flex;
        justify-content: space-between;
        margin-bottom: 27px;
    }
    .footer-nav-links-text-arrow {
        display: block;
    }
    .footer-nav-links {
        border-bottom: 1px solid #c4c4c4;
        border-top: 1px solid #c4c4c4;
        flex-direction: column;
        margin-top: 16px;
        padding: 24px 16px;
    }
    .footer-nav-links-text-arrow {
        display: block;
    }

    }

