.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: .03em;
  line-height: 19px;
  padding-top: 29px;
  z-index: 10;
}

.App-link {
  color: #61dafb;
}



.menu-content {
  display: flex;
  flex-direction: column;
  position: relative;
}

.menu-content-actions {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-bottom: 32px;
  width: 100%;
}

.menu_logo {
  display: flex;
}

.App-link {
  align-items: center;
  color: #6a6a6a;
  display: flex;
  text-decoration: none;
  text-decoration: initial;
}

.ogb,
.outline-green-button {
  background: inherit;
  border: 1px solid #367a54;
  border-radius: 16px;
  color: #367a54 !important;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: .03em;
  line-height: 19px;
  outline: none;
  display: block !important;
}

.outline-green-button:hover {
  fill: #fff;
  background-color: #367a54;
  color: #fff!important;;
  cursor: pointer;
  transition: .3s;
}

.filled-green-button {
  display: block !important;
  background: #367a54;
  border: 1px solid #367a54;
  border-radius: 16px;
  color: #fff !important;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: .03em;
  line-height: 19px;
  outline: none;

}

.filled-green-button:hover {
  cursor: pointer;
  transition: .3s;
}

.button:hover {
  cursor: pointer;
  opacity: .7;
}

.public-actions-routes-login,
.public-actions-routes-register {
  padding: 11px;
  width: 140px;
}

.public-actions-routes-register {
  margin-left: 12px;
}

.menu-actions img {
  margin-left: 24px;
  display: none;
}

.menu-actions img.active_open{display: block;}
.menu-actions img.active_close{display: block;}
.menu-actions {
  align-items: center;
  display: flex;
  justify-content: center;
}

.container {
  display: flex;
  margin: 0 auto;
  max-width: 1360px;
}

.public-actions-mobile {
  display: none;
}

.public-actions-routes {
  display: flex;
}

input.checkbox-box-menu[type=checkbox] {
  cursor: pointer;
  height: 100%;
  right: 0;
  opacity: 0;
  position: absolute;
  top: -15px;
  width: 20px;
}
.public-actions-mobile-content{display: none;}
.public-actions-mobile-content.active_menu {
  background: #eae9e5;
  display: flex;
  justify-content: center;
  left: -20px;
  margin: 0 auto;
  position: absolute;
  width: 100vw;
  height: 100vh;
}
.public-actions-routes {
  display: flex;
}

@media (max-width: 500px){
.register-form-checkbox {
    align-items: flex-start!important;
    
}
.register-form-checkbox-label {
  flex-wrap: wrap;
  width: 240px;
}
.register-form-checkbox-label {
  flex-wrap: wrap;
  width: 240px;
}
.register-form-checkbox-label span {
  white-space: pre-wrap;
}
}

@media (max-width: 768px) {
  .public-actions-routes-register {
    margin: 40px 0 0;
}
  .public-actions-routes {
    flex-direction: column;
    margin-top: 226px;
}
  .public-actions-mobile {
    display: block;
  }

  .public-actions-content {
    display: none;
  }
}

@media (max-width: 1400px) {
  .container {
    margin: 0 20px;
    width: auto;
  }
}