
  .wallet-promo {
    display: flex;
    justify-content: space-between;
    margin-top: 160px;
  }
  .wallet-promo-main {
    display: flex;
    flex: 2 1;
    flex-direction: column;
    justify-content: center;
  }
  .wallet-promo-main-title {
    font-family: Spartan,sans-serif;
    font-size: 48px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: .03em;
    line-height: 54px;
    text-align: center;
  }
  .wallet-promo-main-description {
    font-family: Roboto,sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: .03em;
    line-height: 28px;
    margin-top: 24px;
    text-align: center;
  }
  
  .filled-green-button {
    background: #367a54;
    border: 1px solid #367a54;
    border-radius: 16px;
    color: #fff;
    font-family: Roboto,sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: .03em;
    line-height: 19px;
    outline: none;
  }
  .wallet-promo-main-button {
    height: 64px;
    margin: 40px auto 0;
    width: 340px;
  }
  .promo-phone {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: 60px auto;
    position: relative;
  }
  .promo-phone-pc {
    position: relative;
    z-index: 2;
  }
  .promo-phone-circle {
    bottom: 0;
    left: 50%;
    position: absolute;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: 1;
  }
  .promo-phone-mobile, .promo-phone-mobile-circle {
    display: none;
  }
  
  @media (max-width: 768px){
.wallet-promo {
    margin-top: 80px;
}
.wallet-promo-main-description, .wallet-promo-main-title {
  margin: 0 auto;
  width: 300px;
}
.wallet-promo-main-description {
  margin-top: 24px;
}
.wallet-promo-main-button {
  height: 40px;
  width: 180px;
}
.promo-phone-circle, .promo-phone-pc {
  display: none;
}
.promo-phone-mobile {
  display: flex;
  z-index: 2;
}
.promo-phone-mobile-circle {
  bottom: 0;
  display: flex;
  left: 50%;
  position: absolute;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  z-index: 1;
}


  }



  @media (max-width: 1024px){
.promo-phone-circle {
    width: 760px;
}


  }