.app, .app-main {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.login {
    background: #eae9e5;
}

.page-main {
    background: #eae9e5;
    border-top: 1px solid #c4c4c4;
    height: auto;
    padding-bottom: 100px;
}

.form-container {
    display: flex;
    flex-direction: column;
    margin: 85px auto 0!important;
    max-width: 464px!important;
}

.form-container-content {
    background: #fff;
    border-radius: 10px;
    padding: 40px 32px;
    position: relative;
}

.form-container-content-label {
    align-items: center;
    display: flex;
    font-family: Spartan,sans-serif;
    font-size: 26px;
    font-style: normal;
    font-weight: 600;
    justify-content: center;
    letter-spacing: .03em;
    line-height: 29px;
}

.login-form, .login-form-content {
    display: flex;
    flex-direction: column;
}
.input {
    display: flex;
    flex-direction: column;
    position: relative;
}
.login-form {
    position: relative;
}

.login-form-content-input {
    margin-top: 41px;
}

.input-field, .input-label {
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
}

.input-error {
    color: #ec0000;
    top: calc(100% + 10px);
}
.input-detail {
    color: #367a54;
    right: 20px;
}

.input-detail, .input-error {
    font-family: Spartan,sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: .03em;
    line-height: 13px;
    position: absolute;
}
.input-reveal {
    position: absolute;
    right: 15px;
}
.input-reveal.active svg circle{stroke:rgb(54, 122, 84);}
.input-reveal.active svg path{fill:rgb(54, 122, 84);;}
.input-reveal.active svg path.strokecolor{stroke:rgb(54, 122, 84);}
.input-detail, .input-reveal {
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}

.input-field {
    border: 1px solid #c4c4c4;
    border-radius: 16px;
    box-sizing: border-box;
    font-family: Roboto,sans-serif;
    height: 54px;
    letter-spacing: .03em;
    line-height: 19px;
    outline-color: #367a54;
    padding: 0 60px 0 24px;
}

.filled-green-button {
    background: #367a54;
    border: 1px solid #367a54;
    border-radius: 16px;
    color: #fff;
    font-family: Roboto,sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: .03em;
    line-height: 19px;
    outline: none;
}
.filled-green-button:disabled {
    background-color: #eae9e5!important;
    border-color: #eae9e5!important;
    color: #6a6a6a!important;
    cursor: no-drop;
}
.login-form-button {
    margin-top: 51px;
    padding: 23px 0 22px;
}

.login-form-forgot {
    display: flex;
    justify-content: center;
    margin-top: 32px;
}

.route-link {
    align-items: center;
    color: #6a6a6a;
    display: flex;
    text-decoration: none;
    text-decoration: initial;
}
.login-form-forgot span {
    color: #dd5c29;
    font-family: Roboto,sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: .03em;
    line-height: 18px;
}


/* checkbox */
.register-form-checkbox {
    display: flex;
    margin-top: 16px;
}

.checkbox {
    align-items: center;
    display: flex;
    position: relative;
}

.checkbox input[type=checkbox] {
    cursor: pointer;
    height: 100%;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    width: 16px;
}

.checkbox .checkmark {
    align-items: center;
    background: inherit;
    border: 2px solid #6a6a6a;
    border-radius: 2px;
    display: inline-flex;
    height: 16px;
    justify-content: center;
    margin-right: 14px;
    width: 16px;
}

.checkbox .checkmark img.false {
    display: none;
    height: 8px;
    width: 11px;
}
.checkbox .checkmark img.true {
    display: block;
    height: 8px;
    width: 11px;
}

.checkbox label {
    color: #6a6a6a;
    font-family: Roboto,sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: .03em;
    line-height: 18px;
}

.register-form-checkbox-label-link {
    color: #367a54;
}
.register-form-checkbox-label {
    display: flex;
    white-space: pre;
}


/* mobile */

@media (max-width: 768px){
.form-container {
    margin: 98px auto 40px;
    padding: 0 16px;
}
.form-container-content {
    padding: 32px 24px;
}
.login-form-forgot {
    margin-top: 24px;
}
}