.products {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
}

@media (max-width: 768px){
    
    .product-card {
        padding: 32px 16px 20px;
    }
    .product-card-footer-image {
        display: none;
    }
    .product-card-footer-mobile {
        display: block!important;
    }
    .products-card {
        margin-left: 0px!important;
    }
    }
      @media (max-width: 1024px){
   
        .products-card, .products-wallet {
            flex: auto!important;
            min-height: 350px!important;
            width: 100%;
        }
        .products-card-image, .products-wallet-image {
            bottom: 0;
        }
        .products-card {
            margin: 32px 0 0;
        }
    }